/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* Extra */
a {
  text-decoration: none;
}

li {
  list-style: none;
}

.navbar {
  width: 100%;
  height: 60px;
  line-height: 50px;
  text-align: center;
  font-family: sans-serif;
  background-color: #f4f4f4;
}

.menu {
  height: 100%;
  float: left;
}
.menu .faColor {
  color: #9a9b9a;
}
.menu ul {
  width: 100%;
  height: inherit;
  margin: 0;
  padding: 0;
  /* OLD - iOS 6-, Safari 3.1-6 */
  /* OLD - Firefox 19- (buggy but mostly works) */
  /* TWEENER - IE 10 */
  /* NEW - Chrome */
  display: flex;
  /* NEW, Spec - Opera 12.1, Firefox 20+ */
}
.menu ul a {
  width: -moz-fit-content;
  width: fit-content;
  height: inherit;
  display: inline-block;
  margin-right: 30px;
  color: #9a9b9a;
  font-size: 13;
  border-radius: 3px;
}
.menu ul a i {
  font-size: 13;
  color: #9c577d;
}
.menu ul a:hover {
  background: #a2a8ab;
  transition: 0.5s;
}
.menu #menuToggle {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 768px) {
  .menu {
    width: 100%;
    height: auto;
  }

  .menu ul {
    display: block;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s;
  }

  .menu ul a {
    text-align: left;
    width: 100%;
    height: 50px;
    background-color: #f4f4f4;
    padding: 10px 0px 10px 5%;
  }

  .menu-icon {
    width: 100px;
    height: inherit;
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    line-height: 60px;
  }

  #menuToggle:checked ~ ul {
    max-height: 100%;
  }

  .menu-icon i {
    font-size: 1.7em;
  }
}
#documentTable {
  color: #716565;
  width: 100%;
  border-collapse: collapse;
  font-family: "Open Sans", sans-serif;
}
#documentTable tr th {
  border-bottom: 1px solid #d8d8d8;
  padding: 8px;
  text-align: left;
}
#documentTable tr th i {
  margin-right: 5vh;
}
#documentTable tr td {
  border-bottom: 1px solid #fcfcfc;
  padding: 8px;
}
#documentTable tr td i {
  margin-right: 5vh;
}

#documentTableHeader {
  font-family: "Open Sans", sans-serif;
  font-weight: normal;
}